.PagNot{
    &-title{
        display: flex;
        justify-content: center;
        font-size: 25px;
        font-size: $fontText;
        margin-bottom: 30px;
    }
    &-text{
        display: flex;
        justify-content: center;
        font-size: 15px;
        color:$colorpalette3;
        font-size: $fontText;
        margin-bottom: 30px;
    }
    &-link{
        margin-left: 5px;
        color:$colorpalette2;
    }
    &-image{
        height: 20px;
        transform: translate(0,4px);
    }
}
   
